
import moment from '@/common/moment';

export default {
  props: {
    movie: {
      type: Object
    },
    timeAgo: {
      type: String
    },
    mini: {
      type: Boolean
    }
  },
  setup(props) {},
  computed: {
    progress() {
      return moment.format(parseInt(this.movie?.last_watch_progress ?? 0));
    }
  }
};
