<template>
  <div :class="['ht-item', mini && 'mini']">
    <a :href="`/movie/${movie.id}`" class="movie-cover">
      <div class="cover-img">
        <img :src="movie.cover" alt="" />
      </div>
    </a>
    <div class="item-info">
      <a
        :href="`/movie/${movie.id}`"
        :class="['info-title', mini ? 'text-ellipsis' : 'webkit-text-ellipsis']"
        :title="movie.name"
      >
        {{ movie.name || movie.introduction }}
      </a>
      <div class="info-meta">
        <div class="meta-text text-ellipsis">
          {{
            movie?.count_series > 1
              ? '观看至 第' + (movie.last_watch_series + 1) + '集 ' + progress
              : '观看至 ' + progress
          }}
        </div>
        <div v-if="timeAgo" class="meta-text text-ellipsis">{{ timeAgo }}</div>
        <div v-else-if="!!movie.actors" class="meta-text text-ellipsis">{{ '主演：' + movie.actors }}</div>
        <div v-else-if="!!movie.producer" class="meta-text text-ellipsis">{{ '导演：' + movie.producer }}</div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import moment from '@/common/moment';

export default {
  props: {
    movie: {
      type: Object
    },
    timeAgo: {
      type: String
    },
    mini: {
      type: Boolean
    }
  },
  setup(props) {},
  computed: {
    progress() {
      return moment.format(parseInt(this.movie?.last_watch_progress ?? 0));
    }
  }
};
</script>

