<template>
  <div id="root">
    <router-view :key="$route.fullPath" />
  </div>
  <LoginModal />
  <ReportModal />
  <PWAInstallPrompt name="剧好看" :previews="previews" />
</template>

<script lang="ts">
import LoginModal from '@/views/parts/LoginModal.vue';
import ReportModal from '@/views/parts/ReportModal.vue';
import PWAInstallPrompt from '@/views/parts/PWAInstallPrompt.vue';
import store, { TYPES, Storage } from '@/store';
import { useSeoGroupQuery } from '@/graphql/hooks';

export default {
  components: {
    LoginModal,
    ReportModal,
    PWAInstallPrompt
  },
  setup(props) {
    const user = Storage.getItem('user');
    if (user?.id) {
      store.commit(TYPES.USER_LOGIN, user);
    }
    const seos = Storage.getItem('seos');
    if (seos?.keywords) {
      document.querySelector('meta[name="keywords"]').setAttribute('content', seos.keywords || '');
    }
    if (seos?.description) {
      document.querySelector('meta[name="description"]').setAttribute('content', seos.description || '');
    }
    if (seos?.title) {
      document.title = seos?.title;
    }
    // 更新SEO内容
    useSeoGroupQuery({ group: 'TDK' });
  },
  data() {
    return {
      previews: [
        'https://juhaokantv.com/images/screens/1.png',
        'https://juhaokantv.com/images/screens/2.png',
        'https://juhaokantv.com/images/screens/3.png',
        'https://juhaokantv.com/images/screens/4.png',
        'https://juhaokantv.com/images/screens/5.png',
        'https://juhaokantv.com/images/screens/6.png',
        'https://juhaokantv.com/images/screens/7.png'
      ]
    };
  }
};
</script>

