import { Ref } from 'vue-demi';
import { ref, onMounted, watch } from 'vue';
import { useQuery, useResult, UseQueryOptions } from '@vue/apollo-composable';
import { RELATED_MOVIES_QUERY } from '@/graphql';

interface Variables {
  movie_id: Ref<Number> | Number;
  limit?: Ref<Number> | Number;
}
export default function useRelatedMoviesQuery(variables: Variables, options?: UseQueryOptions) {
  const { result, loading, error, refetch } = useQuery(RELATED_MOVIES_QUERY, variables, options);
  const relatedMovies = useResult(result, [], (data: any) => data?.relatedMovies);

  return {
    loading,
    error,
    refetch,
    relatedMovies
  };
}
