
import { ref, computed } from 'vue';
import { ElNotification } from 'element-plus';

const PWA_PROMPT = (window.location.host + '-> Storage.pwaPrompt').toLocaleUpperCase();
type OS = 'IOS' | 'Android' | 'PC';
const CLIENT_OS: OS = (function(): OS {
  let os: OS;
  if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
    os = 'IOS';
  } else if (/(Android)/i.test(navigator.userAgent)) {
    os = 'Android';
  } else {
    os = 'PC';
  }
  return os;
})();

function localStorageSetItem(key, value, maxAge?: number) {
  const VALUE = {
    value,
    maxAge,
    timestamp: Date.now()
  };
  localStorage.setItem(key, JSON.stringify(VALUE));
}

function localStorageGetItem(key) {
  let item: any = localStorage.getItem(key);
  try {
    item = JSON.parse(item);
  } catch (err) {}
  if (item && item.value) {
    if (item.maxAge && Date.now() - item.timestamp > item.maxAge * 1000) {
      localStorage.removeItem(key);
      return null;
    } else {
      return item.value;
    }
  }
}

export default {
  props: {
    name: {
      type: String,
      require: true
    },
    host: {
      type: String,
      default() {
        return window.location.host;
      }
    },
    description: {
      type: String,
      default() {
        return '点击按钮下载App，功能更丰富，流畅又高清，随时发现更多精彩影视，解锁追剧新姿势。';
      }
    },
    previews: {
      type: Array,
      require: true
    },
    maxAge: {
      type: Number,
      default() {
        // 用户关闭提示后一个星期内不提示
        return 604800;
      }
    }
  },
  beforeMount() {
    this.prompted = localStorageGetItem(PWA_PROMPT);
  },
  mounted() {
    if (!this.prompted) {
      if ('onbeforeinstallprompt' in window) {
        this.pwaEventListener();
      } else if (CLIENT_OS !== 'PC') {
        this.visible = true;
      }
    }
  },
  methods: {
    installNotification() {
      return ElNotification({
        offset: 80,
        duration: 0,
        showClose: false,
        dangerouslyUseHTMLString: true,
        position: 'bottom-right',
        customClass: CLIENT_OS == 'PC' ? 'pwainstall-notification' : 'pwainstall-notification h5',
        title: this.name,
        message: `<div class="pwa-notification"><div class="pwa-notification__group"><div class="group-content">添加快捷方式到桌面，流畅又高清，随时发现更多精彩影视，解锁追剧新姿势。</div><div class="group-button"><button class="pwa-notification__button close" onclick="window.dispatchEvent(new CustomEvent('dismissinstallprompt'));">取消</button><button class="pwa-notification__button" onclick="window.dispatchEvent(new CustomEvent('installpromptchoice'));">安装</button><div/></div></div></div>`
      });
    },
    downloadClick() {
      window.open('https://yy.juhaokantv.com/juhaokan_4.0.8_1.apk');
    },
    pwaEventListener() {
      let deferredPrompt;

      const installprompt = e => {
        e.preventDefault();
        deferredPrompt = e;
        this.showInstallPromotion();
        console.log(`'beforeinstallprompt' event was fired.`);
      };

      window.addEventListener('beforeinstallprompt', installprompt);

      window.addEventListener('installpromptchoice', async () => {
        console.log('await installpromptchoice');
        window.removeEventListener('beforeinstallprompt', installprompt);
        this.hideInstallPromotion();
        deferredPrompt.prompt();
        const { outcome } = await deferredPrompt.userChoice;
        console.log(`User response to the install prompt: ${outcome}`);
        if (outcome === 'dismissed') {
          //用户取消安装，一段时间内不再提示
          localStorageSetItem(PWA_PROMPT, true, this.maxAge);
        } else {
          //用户选择安装，不再提示安装
          // localStorageSetItem(PWA_PROMPT, true);
        }
        deferredPrompt = null;
      });

      window.addEventListener('appinstalled', () => {
        this.hideInstallPromotion();
        deferredPrompt = null;
        console.log('PWA was installed');
      });

      window.addEventListener('dismissinstallprompt', () => {
        this.hideInstallPromotion();
        deferredPrompt = null;
        console.log('PWA was dismissed');
      });
    },
    showInstallPromotion() {
      this.notify = this.installNotification();
    },
    hideInstallPromotion() {
      this.notify?.close();
      localStorageSetItem(PWA_PROMPT, true, this.maxAge);
    },
    hidePrompt() {
      this.visible = false;
    }
  },
  data() {
    return {
      visible: false,
      notify: null,
      isIOS: CLIENT_OS === 'IOS'
    };
  }
};
