// @ts-nocheck

import { ref, watch, computed } from 'vue';
import { useQuery, useResult, UseQueryOptions } from '@vue/apollo-composable';
import { FOLLOWED_USERS_QUERY, USER_FOLLOWERS_QUERY } from '@/graphql';
import { mergeProperty } from '@/common';

interface FollowVariables {
  user_id: Number;
  page?: Number;
  filter?: String;
}

export function useFollowedUsersQuery(variables: FollowVariables, options?: UseQueryOptions) {
  const { result, loading, error, fetchMore, refetch } = useQuery(FOLLOWED_USERS_QUERY, variables, options);
  const followedUsers = useResult(result, null, (data: any) => data?.follows.data);
  const paginatorInfo = useResult(result, {}, (data: any) => data?.follows?.paginatorInfo);
  const currentPage = ref(paginatorInfo?.currentPage);
  const hasMorePages = ref(paginatorInfo?.hasMorePages);

  watch(paginatorInfo, (value, prevValue) => {
    currentPage.value = value?.currentPage;
    hasMorePages.value = value?.hasMorePages;
  });

  const loadingMore = ref(false);
  const isFinished = computed(() => !loading.value && !error.value && !hasMorePages.value);
  const disabled = computed(() => loading.value || loadingMore.value || !hasMorePages.value);

  function loadMore() {
    if (hasMorePages.value && !loadingMore.value) {
      loadingMore.value = true;
      fetchMore({
        variables: {
          page: currentPage.value + 1
        },
        updateQuery: (prev: any, { fetchMoreResult }) => {
          loadingMore.value = false;
          if (!fetchMoreResult) {
            return prev;
          }
          return mergeProperty(prev, fetchMoreResult);
        }
      });
    }
  }

  return {
    loading,
    error,
    refetch,
    followedUsers,
    loadMore,
    loadingMore,
    isFinished,
    disabled
  };
}

