<template>
  <div id="report-dialog" v-loading.fullscreen.lock="loading">
    <el-dialog v-model="visible" custom-class="report-modal" :title="modalTitle" width="90%" destroy-on-close center>
      <el-radio-group v-model="reasonType">
        <div class="radio-wrap col-xs-4" v-for="(type, index) in types" :key="index">
          <el-radio :label="type">{{ type }}</el-radio>
        </div>
      </el-radio-group>
      <el-input
        v-model="content"
        maxlength="140"
        placeholder="请详细描述举报原因，便于平台判断违规情况"
        show-word-limit
        type="textarea"
        class="input-style"
        resize="none"
      >
      </el-input>
      <el-button type="primary" :disabled="disabled" @click="onSubmit" class="submit-button">
        提交
      </el-button>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, ref, toRefs } from 'vue';
import { ElMessage } from 'element-plus';
import { exceptionCapture, errorMessage } from '@/common';
import { useQuery, useMutation, UseMutationOptions } from '@vue/apollo-composable';
import { CREATE_REPORT_MUTATION } from '@/graphql';

const TYPES = ['垃圾广告', '色情低俗', '违法犯罪', '恶意营销', '血腥暴力', '感官不适', '时政发动', '其他'];

export default defineComponent({
  setup() {
    const visible = ref(false);
    const reportId = ref();
    const reportType = ref('movies');
    const reasonType = ref('');
    const content = ref('');

    const { mutate: createReportMutation, loading } = useMutation(CREATE_REPORT_MUTATION, () => ({
      variables: {
        id: reportId.value,
        type: reportType.value,
        reason: content.value ? `${reasonType.value}：${content.value}` : reasonType.value
      },
      fetchPolicy: 'network-only'
    }));

    async function onSubmit() {
      const [err, res] = await exceptionCapture(() => createReportMutation());
      if (err) {
        ElMessage.error(`举报出错：${errorMessage(err)}`);
      } else if (res?.data.createReport) {
        visible.value = false;
        ElMessage({ message: '举报成功!', type: 'success' });
      }
    }

    return {
      visible,
      loading,
      reportId,
      reportType,
      reasonType,
      content,
      onSubmit
    };
  },
  computed: {
    disabled() {
      return !this.reportId || !this.reasonType;
    }
  },
  mounted() {
    this.$bus.on('SHOW_REPORT_MODAL', ({ id, type, title }) => {
      this.reportId = id;
      if (type) {
        this.reportType = type;
      }
      if (title) {
        this.modalTitle = title;
      }
      this.visible = true;
    });
  },
  data() {
    return {
      types: TYPES,
      modalTitle: '举报'
    };
  }
});
</script>

