      </div>
      <div class="mod-rank-list">
        <router-link class="rank-item" v-for="(item, index) in sideMovies" :key="item.id" :to="`/movie/${item?.id}`">
          <img class="item-img" :src="item.cover" />
          <span class="item-num">{{ index + 1 }}</span>
          <div class="item-center">
            <div class="item-title text-ellipsis">
              {{ item.name }}
            </div>
            <div class="item-desc text-ellipsis">
              {{ item.introduction }}
            </div>
