<template>
  <div id="search-bar">
    <el-autocomplete
      clearable
      class="search-input"
      v-model="keyword"
      :fetch-suggestions="querySearch"
      :placeholder="`${keyword || '搜索你想看 de 视频'}`"
      @keyup.enter="onSearch"
      @focus="changeStyle('block', '.el-autocomplete-suggestion')"
    >
      <template #default="{ item }">
        <div v-if="!keyword && searchLogs.indexOf(item) === 0" class="logs-title">
          搜索历史<span class="close" @click="deleteSearchLogs">清除全部</span>
        </div>
        <div v-if="!keyword" class="history-item" @click="onSearch({ q: item })">
          {{ item }}
          <i class="el-icon-close close" @click.stop="deleteSearchLogs(item)"></i>
        </div>
        <div v-else class="recommend-item" @click="onSearch({ q: item.name })">
          {{ item.name }}
        </div>
      </template>
      <template #prefix>
        <div class="input-icon">
          <svg class="icon" fill="rgba(47, 48, 53, 0.4)" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 18 18">
            <path
              fill-rule="evenodd"
              clip-rule="evenodd"
              d="M7.875 1.5a6.375 6.375 0 103.642 11.608l3.063 3.063a1.125 1.125 0 001.59-1.591l-3.062-3.063A6.375 6.375 0 007.875 1.5zM3.75 7.875a4.125 4.125 0 118.25 0 4.125 4.125 0 01-8.25 0z"
            ></path>
          </svg>
        </div>
      </template>
      <template #append>
        <button :class="['input-button', keyword && 'active']" :disabled="!keyword" @click="onSearch">
          <span class="btn-title">搜索</span>
        </button>
      </template>
    </el-autocomplete>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from 'vue';
import { useRouter, useRoute } from 'vue-router';
import { useApolloClient } from '@vue/apollo-composable';
import { exceptionCapture } from '@/common';
import store, { Storage, TYPES } from '@/store';
import { SEARCH_MOVIES_QUERY } from '@/graphql';

export default defineComponent({
  setup(props) {
    const router = useRouter();
    const route = useRoute();
    const client = useApolloClient().client;
    const keyword = ref(route.query?.keyword);
    const searchLogs = ref(Storage.getItem('searchLogs') || []);
    const searchResult = ref();
    const deleteSearchLogs = queryString => {
      const index = searchLogs.value.indexOf(queryString);
      if (index >= 0) {
        searchLogs.value.splice(index, 1);
      } else {
        searchLogs.value = [];
      }
      Storage.setItem('searchLogs', searchLogs.value);
    };
    const saveSearchLogs = queryString => {
      const newLogs = new Set([queryString, ...searchLogs.value]);
      searchLogs.value = [...newLogs].slice(0, 10);
      Storage.setItem('searchLogs', searchLogs.value);
    };

    const querySearch = async (queryString: string, cb) => {
      const result = await (queryString ? searchMovies(queryString) : searchLogs.value);
      searchResult.value = result;
      // 调用 callback 返回列表数据
      cb(result);
    };
    const searchMovies = async (queryString: string) => {
      const [err, res] = await exceptionCapture(() =>
        client.query({ query: SEARCH_MOVIES_QUERY, variables: { keyword: queryString } })
      );
      return res?.data?.searchMovies?.data || [];
    };

    // 输入建议框显示/消失
    const changeStyle = (status, className) => {
      let dom = document.querySelectorAll(className);
      dom[0].style.display = status;
    };

    const onSearch = ({ q }) => {
      const text = q || keyword.value || '';
      if (text) {
        saveSearchLogs(text);
        keyword.value = text;
        router.push({ path: '/search', query: { keyword: text } });
        changeStyle('none', '.el-autocomplete-suggestion');
      }
    };

    onMounted(() => {
      keyword.value = route.query?.keyword || '';
    });

    return {
      keyword,
      querySearch,
      searchLogs,
      searchResult,
      onSearch,
      changeStyle,
      deleteSearchLogs
    };
  }
});
</script>

