import { gql } from '@apollo/client/core';

// 关注的用户
export const FOLLOWED_USERS_QUERY = gql`
  query followedUsersQuery($user_id: Int!, $page: Int, $filter: FollowedEnumType) {
    follows(user_id: $user_id, page: $page, followed_type: "users", filter: $filter) {
      paginatorInfo {
        currentPage
        hasMorePages
      }
      data {
        id
        name
        people {
          id
          age
          name
          avatar
          gender
          count_articles
          count_likes
          introduction
          followed_status
          count_followings
          count_followers
        }
      }
    }
  }
`;

// 用户的粉丝
export const USER_FOLLOWERS_QUERY = gql`
  query userFollowersQuery($user_id: Int!, $page: Int, $filter: FollowedEnumType) {
    followers(followed_id: $user_id, page: $page, followed_type: "users", filter: $filter) {
      paginatorInfo {
        currentPage
        hasMorePages
      }
      data {
        id
        user {
          id
          age
          name
          avatar
          gender
          count_articles
          count_likes
          introduction
          followed_status
          count_followings
          count_followers
        }
      }
    }
  }
`;

export const TOGGLE_FOLLOW_MUTATION = gql`
  mutation toggleFollowMutation($id: Int!, $type: FollowedEnumType!) {
    toggleFollow(id: $id, type: $type) {
      id
      isFollowed
    }
  }
`;
