import { Ref } from 'vue-demi';
import { ref, onMounted, watch } from 'vue';
import { useQuery, useResult, UseQueryOptions } from '@vue/apollo-composable';
import { MOVIE_QUERY } from '@/graphql';

interface Variables {
  id: Ref<Number> | Number;
  key?: String;
}
export default function useMovieQuery(variables: Variables, options?: UseQueryOptions) {
  const { result, loading, error, refetch } = useQuery(MOVIE_QUERY, variables, options);
  const movie = useResult(result, null, (data: any) => data?.movie);

  return {
    loading,
    error,
    refetch,
    movie
  };
}
