<template>
  <el-form ref="resetFrom" class="reset-form" :model="resetData" :rules="rules">
    <div class="code-label">验证码已通过短信发送至 +86 {{ resetData.phone }}</div>
    <el-form-item label="验证码" prop="code" class="code-item">
      <el-input
        name="resetData-code"
        class="code-input"
        v-model="resetData.code"
        placeholder="请输入验证码"
        maxlength="6"
        autocomplete
        clearable
      >
        <template #append>
          <div @click="countRef < 1 && getCodeAgain()" :class="['timer', countRef < 1 && 'resend']">
            <span>{{ countRef > 0 ? countRef + '秒后重新获取' : '重新获取' }}</span>
          </div>
        </template>
      </el-input>
    </el-form-item>

    <el-form-item label="密&emsp;码" prop="newPassword">
      <el-input v-model="resetData.newPassword" placeholder="请输入密码" show-password maxlength="12"></el-input>
    </el-form-item>

    <el-form-item class="button-wrap">
      <el-button type="primary" :disabled="disabled" @click="submitForm" class="submit-button">
        <i class="el-icon-loading" v-if="loading" />
        确认重置
      </el-button>
    </el-form-item>
  </el-form>
</template>

<script lang="ts">
import { ref, toRefs, watchEffect } from 'vue';
import { ElMessage } from 'element-plus';
import { useMutation } from '@vue/apollo-composable';
import { exceptionCapture, errorMessage } from '@/common';
import { SEND_VERIFY_CODE, RESET_PASSWORD } from '@/graphql';

export default {
  props: {
    phone: {
      type: String
    },
    toggleTab: {
      type: Function,
      default: () => {}
    }
  },
  setup(props, context) {
    const loading = ref(false);
    const countRef = ref(59);
    const { phone } = toRefs(props);
    const resetData = ref({ phone: phone.value, code: '', action: 'RESET_PASSWORD', newPassword: '' });

    const { mutate: sendCodeMutation } = useMutation(SEND_VERIFY_CODE, () => ({
      variables: {
        phone: resetData.value.phone,
        action: 'RESET_PASSWORD'
      }
    }));

    const { mutate: resetPasswordMutation } = useMutation(RESET_PASSWORD, () => ({
      variables: resetData.value
    }));

    function countDownTime() {
      countRef.value = 59;
      let countdown = setInterval(() => {
        let c = countRef.value - 1;
        if (c < 0) {
          clearInterval(countdown);
        } else {
          countRef.value = c;
        }
      }, 1000);
    }

    async function getCodeAgain() {
      const [err, res] = await exceptionCapture(() => sendCodeMutation());
      if (err) {
        ElMessage.error(`获取验证码出错：${errorMessage(err)}`);
      } else if (res?.data.sendVerifyCode) {
        countDownTime();
        ElMessage({ message: '验证码发送成功!', type: 'success' });
      }
    }

    watchEffect(() => {
      countDownTime();
    });

    return {
      loading,
      phone,
      resetData,
      countRef,
      getCodeAgain,
      resetPasswordMutation
    };
  },
  computed: {
    disabled() {
      return !this.resetData.phone || !this.resetData.code || !this.resetData.newPassword;
    }
  },
  methods: {
    toggleForm() {
      this.$refs.resetFrom.resetFields();
      this.toggleTab();
    },
    submitForm() {
      this.$refs.resetFrom.validate(async valid => {
        if (valid) {
          this.loading = true;
          const [err, res] = await exceptionCapture(() => this.resetPasswordMutation());
          if (err) {
            this.loading = false;
            ElMessage.error(`重置密码出错：${errorMessage(err)}`);
          } else if (res?.data.retrievePassword) {
            this.loading = false;
            ElMessage({ message: '密码修改成功，请重新登录!', type: 'success' });
            this.toggleForm();
          }
        } else {
          ElMessage.error('格式错误！');
          return false;
        }
      });
    }
  },
  data() {
    return {
      rules: {
        code: [
          {
            required: true,
            message: '请输入验证码',
            trigger: 'blur'
          },
          {
            min: 4,
            max: 6,
            message: '验证码长度不正确（4-6）位',
            trigger: 'blur'
          }
        ],
        newPassword: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 12,
            message: '密码格式不正确（6-12）位',
            trigger: 'blur'
          }
        ]
      }
    };
  }
};
</script>

