import { Ref } from 'vue-demi';
import { ref, onMounted, watch } from 'vue';
import { useQuery, useMutation, UseMutationOptions } from '@vue/apollo-composable';
import { SIGN_UP } from '@/graphql';

interface Variables {
  account: String;
  password: String;
}

export default function useSignUpMutation(variables: Variables, options?: UseMutationOptions) {
  const user = ref({});
  const { mutate: signUpMutate, onDone, onError, loading } = useMutation(SIGN_UP, () => ({ variables, options }));

  onDone(res => {
    user.value = res?.data?.signUp;
  });

  return {
    signUpMutate,
    onDone,
    onError,
    loading,
    user
  };
}
