<template>
  <div id="recommend">
    <div class="fluid-content">
      <Skeleton v-if="sticksData?.length < 1" type="movies" />
      <template v-else>
        <div class="module-wrap">
          <GridMovies
            title="电视剧"
            sideTitle="电视剧热播榜单"
            :movies="sticksData?.[0]?.editorChoice?.movies?.data"
            :sideMovies="sticksData?.[1]?.editorChoice?.movies?.data"
          />
        </div>
        <div class="module-wrap">
          <GridMovies
            title="电影"
            sideTitle="电影热播榜单"
            :movies="sticksData?.[2]?.editorChoice?.movies?.data"
            :sideMovies="sticksData?.[3]?.editorChoice?.movies?.data"
          />
        </div>
        <div class="module-wrap">
          <GridMovies
            title="动漫"
            sideTitle="动漫热播榜单"
            :movies="sticksData?.[4]?.editorChoice?.movies?.data"
            :sideMovies="sticksData?.[5]?.editorChoice?.movies?.data"
          />
        </div>
        <template v-for="(item, index) in tvSeries" :key="item.id">
          <div class="module-wrap" v-if="item?.editorChoice?.movies?.data?.length > 0">
            <ColumnMovies :title="tvSeriesTitle[index]" :movies="item?.editorChoice?.movies?.data" />
          </div>
        </template>
      </template>
    </div>
  </div>
</template>

<script lang="ts">
import { ref } from 'vue';
import { useQuery, useResult } from '@vue/apollo-composable';
import { STICKS_QUERY } from '@/graphql';
import Skeleton from '@/components/Skeleton.vue';
import MovieItem from '@/components/MovieItem.vue';
import ColumnMovies from '@/views/parts/ColumnMovies.vue';
import GridMovies from '@/views/parts/GridMovies.vue';

export default {
  components: {
    Skeleton,
    MovieItem,
    ColumnMovies,
    GridMovies
  },
  setup(props) {
    const variables = ref({
      place: '网站首页推荐'
    });
    const { result, loading, error } = useQuery(STICKS_QUERY, variables);
    const sticksData = useResult(result, [], (data: any) => data?.sticks?.data);
    // const activities = useResult(sticksData, [], (data: any) => data?.[0]?.editorChoice?.activities);
    const tvSeries = useResult(sticksData, [], (data: any) => data?.slice(6));
    const tvSeriesTitle = ref(['韩剧', '美剧', '日剧', '综艺']);

    return {
      error,
      loading,
      sticksData,
      tvSeries,
      tvSeriesTitle
    };
  }
};
</script>

