<script lang="ts">
import $ from 'jquery';

export default {
  mounted() {
    $(window).on('scroll', () => {
      if ($(window).scrollTop() > 1000) {
        this.visible = true;
      } else {
        this.visible = false;
      }
    });
  },
  methods: {
    goTop() {
      $('body,html').animate({ scrollTop: 0 }, 1000);
    }
  },
  data() {
    return {
      visible: false
    };
  }
};
