import * as TYPES from '../mutation-types';
import Storage from '../storage';

export default {
  state: {
    user: {}
  },
  getters: {
    logged: state => {
      return state.user.id;
    },
    token: state => {
      return state.user.token;
    }
  },
  mutations: {
    [TYPES.USER_LOGIN](state, payload) {
      console.log(TYPES.USER_LOGIN, payload?.id);
      state.user = payload;
    },
    [TYPES.USER_LOGOUT](state) {
      console.log(TYPES.USER_LOGOUT);
      state.user = {};
      Storage.removeItem('user');
    },
    [TYPES.USER_UPDATE](state, payload) {
      console.log(TYPES.USER_UPDATE, payload);
      state.user = Object.assign({}, state.user, payload);
      Storage.setItem('user', state.user);
    }
  }
};
