import { Ref } from 'vue-demi';
import { ref, onMounted, watch } from 'vue';
import { throttle } from 'lodash';

export default function useItemsPerRow(itemName: string): Ref<number> {
  const count = ref(1);
  function getItemsCount() {
    const container = document.getElementById('app');
    var styles = window.getComputedStyle(container);
    count.value = Number(styles.getPropertyValue(itemName));
  }
  getItemsCount();
  onMounted(() => {
    window.addEventListener('resize', throttle(getItemsCount, 200));
  });
  return count;
}
