import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router';
import Home from '../views/Home.vue';
import Recommend from '../views/Recommend.vue';
import Category from '../views/Category.vue';
import User from '../views/User.vue';

// route level code-splitting
// this generates a separate chunk (component.[hash].js) for this route
// which is lazy-loaded when the route is visited.
// component: () => import(/* webpackChunkName: "component" */ '../views/Component.vue');
const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    component: Home,
    children: [
      {
        path: '',
        component: Recommend
      },
      {
        path: '/category',
        component: () => import('../views/Category.vue')
      },
      {
        path: '/videos',
        component: () => import('../views/Videos.vue')
      },
      {
        path: '/profile',
        props: route => ({ query: route.query }),
        component: () => import('../views/Profile.vue')
      }
    ]
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'notfound',
    component: () => import('../views/Notfound.vue')
  },
  {
    path: '/search',
    props: route => ({ query: route.query }),
    component: () => import('../views/Search.vue')
  },
  {
    path: '/movie/:id',
    component: () => import('../views/MovieDetail.vue')
  },
  {
    path: '/video/:id',
    component: () => import('../views/VideoDetail.vue')
  },
  {
    path: '/user/:id',
    props: route => ({ params: route.params, query: route.query }),
    component: () => import('../views/User.vue')
  },
  {
    path: '/login',
    component: () => import('../views/Login.vue')
  },
  {
    path: '/getpass',
    component: () => import('../views/GetPassword.vue')
  },
  {
    path: '/userProtocol',
    component: () => import('../views/UserProtocol.vue'),
    meta: { title: '剧好看-用户协议' }
  },
  {
    path: '/privacyPolicy',
    component: () => import('../views/PrivacyPolicy.vue'),
    meta: { title: '剧好看-隐私政策' }
  }
];

const router = createRouter({
  routes,
  history: createWebHistory(process.env.BASE_URL)
});
router.beforeEach((to, from, next) => {
  window.scrollTo(0, 0);
  next();
});

export default router;
