// @ts-nocheck

import { ref, watch } from 'vue';
import { useQuery, useResult, UseQueryOptions } from '@vue/apollo-composable';
import { SEO_GROUP_QUERY } from '@/graphql';
import store, { TYPES } from '@/store';

interface Variables {
  name?: String;
  group?: String;
}

const seoKeys = ['title', 'keywords', 'description'];

export default function useSeoGroupQuery(variables?: Variables, options?: UseQueryOptions) {
  const { result } = useQuery(SEO_GROUP_QUERY, variables, options);
  const seos = useResult(result, [], (data: any) => data?.seos);
  const group = ref({ title: '', keywords: '', description: '' });

  watch(seos, seo_arr => {
    seoKeys.map(key => {
      let val = seo_arr.find(item => item?.name === key)?.value;
      group.value[key] = val || '';
    });
    store.commit(TYPES.UPDATE_SEOS, group.value);
  });

  return { seos, group };
}
