// @ts-nocheck

import { Ref } from 'vue-demi';
import { ref, toRefs, watch, computed } from 'vue';
import { useQuery, useResult, UseQueryOptions } from '@vue/apollo-composable';
import { CATEGORY_MOVIE_QUERY } from '@/graphql';
import { CategoryMovieQuery } from '@/graphql/schema';
import { mergeProperty } from '@/common';

interface Variables {
  region?: String;
  type?: String;
  country?: String;
  lang?: String;
  style?: String;
  first?: Number;
  page?: Number;
  year?: String;
  scopes?: String;
}
export default function useCategoryMovieQuery(variables?: Ref<Variables | undefined>, options?: UseQueryOptions) {
  const { result, loading, error, fetchMore } = useQuery<{
    categoryMovie: CategoryMovieQuery;
  }>(CATEGORY_MOVIE_QUERY, variables, options);
  const categoryMovie = useResult(result, [], (data: any) => data?.categoryMovie?.data);
  const paginatorInfo = useResult(result, {}, (data: any) => data?.categoryMovie?.paginatorInfo);

  const currentPage = ref(paginatorInfo?.currentPage);
  const hasMorePages = ref(paginatorInfo?.hasMorePages);
  watch(paginatorInfo, (value, prevValue) => {
    currentPage.value = value?.currentPage;
    hasMorePages.value = value?.hasMorePages;
  });
  const loadingMore = ref(false);
  const disabled = computed(() => loading.value || loadingMore.value || !hasMorePages.value);

  function loadMore() {
    console.log('====================================');
    console.log('loadMore', currentPage.value);
    console.log('====================================');
    loadingMore.value = true;
    fetchMore({
      variables: {
        page: currentPage.value + 1
      },
      updateQuery: (prev: any, { fetchMoreResult }) => {
        loadingMore.value = false;
        if (!fetchMoreResult) {
          return prev;
        }
        return mergeProperty(prev, fetchMoreResult);
      }
    });
  }

  return {
    loading,
    error,
    disabled,
    loadMore,
    hasMorePages,
    categoryMovie
  };
}
