// @ts-nocheck

import { Ref } from 'vue-demi';
import { ref, toRefs, watch, computed } from 'vue';
import { useQuery, useResult, UseQueryOptions } from '@vue/apollo-composable';
import { COLLECTION_POSTS_QUERY } from '@/graphql';
import { mergeProperty } from '@/common';

interface Variables {
  id: Ref<number> | number;
  page?: Ref<number> | number;
  count?: Ref<number> | number;
}

export default function useCollectionPostsQuery(variables: Variables, options?: UseQueryOptions) {
  const { result, loading, error, fetchMore } = useQuery(COLLECTION_POSTS_QUERY, variables, options);
  const currentCollection = useResult(result, [], (data: any) => data?.collection);
  const collectionPosts = useResult(result, [], (data: any) => data?.collection?.posts?.data);
  const paginatorInfo = useResult(result, {}, (data: any) => data?.collection?.posts?.paginatorInfo);
  const currentPage = ref(paginatorInfo?.currentPage);
  const hasMorePages = ref(paginatorInfo?.hasMorePages);

  watch(paginatorInfo, (value, prevValue) => {
    currentPage.value = value?.currentPage;
    hasMorePages.value = value?.hasMorePages;
  });

  const loadingMore = ref(false);
  const disabled = computed(() => loading.value || loadingMore.value || !hasMorePages.value);

  function loadMore() {
    if (hasMorePages.value && !loadingMore.value) {
      loadingMore.value = true;
      fetchMore({
        variables: {
          page: currentPage.value + 1
        },
        updateQuery: (prev: any, { fetchMoreResult }) => {
          loadingMore.value = false;
          if (!fetchMoreResult) {
            return prev;
          }
          return mergeProperty(prev, fetchMoreResult);
        }
      });
    }
  }

  return {
    loading,
    error,
    disabled,
    loadMore,
    loadingMore,
    hasMorePages,
    currentCollection,
    collectionPosts
  };
}
