import { gql } from '@apollo/client/core';

export const POST_FRAGMENT = gql`
  fragment postFragment on Post {
    id
    spider {
      id
    }
    description
    cover
    status
    media_type
    liked
    count_visits
    count_likes
    count_comments
    review_id
    review_day
    is_ad
    created_at
    current_episode
    user {
      id
      name
      avatar
      followed_status
      gender
      age
      introduction
      count_likes
      count_followings
      count_followers
    }
    tag_names
    images {
      id
      url
      width
      height
    }
    video {
      id
      url
      cover
      dynamic_cover
      duration
      width
      height
      isHd
      json
    }
    movie {
      id
      name
      introduction
      cover
    }
    collections {
      id
      logo
      name
      description
      updated_to_episode
    }
  }
`;

// 动态详情
export const POST_QUERY = gql`
  ${POST_FRAGMENT}

  query postQuery($id: Int!) {
    post(id: $id) {
      ...postFragment
    }
  }
`;

// 推荐视频刷（快速版）
export const FAST_RECOMMEND_POSTS_QUERY = gql`
  ${POST_FRAGMENT}

  query fastRecommendPostsQuery {
    fastRecommendPosts {
      ...postFragment
    }
  }
`;

// 推荐视频刷（快速版）
export const PUBLIC_POSTS_QUERY = gql`
  ${POST_FRAGMENT}

  query publicPostsQuery($user_id: Int, $first: Int = 10, $page: Int) {
    publicPosts(user_id: $user_id, first: $first, page: $page) {
      paginatorInfo {
        hasMorePages
        currentPage
      }
      data {
        ...postFragment
      }
    }
  }
`;

// 用户发布
// # filter 筛选动态类型
// # enum PostFilter {
// # spider
// # 用户爬取的动态
// # normal
// # 用户发布的动态
// # all
// # 全部
// # }
export const USER_POSTS_QUERY = gql`
  ${POST_FRAGMENT}

  query userPostsQuery($user_id: Int!, $filter: PostFilter, $page: Int, $first: Int) {
    userPosts(user_id: $user_id, filter: $filter, page: $page, first: $first) {
      paginatorInfo {
        hasMorePages
        currentPage
      }
      data {
        ...postFragment
      }
    }
  }
`;
