import { useMutation, UseMutationOptions } from '@vue/apollo-composable';
import { TOGGLE_FAVORITE_MUTATION } from '@/graphql';
import { errorMessage } from '@/common';
import { ElMessage } from 'element-plus';

interface Variables {
  id?: number | any;
  type: String;
}

export default function useFavoriteMutation(variables: Variables, options?: UseMutationOptions) {
  const { mutate: toggleFavorite, onDone, onError, loading } = useMutation(TOGGLE_FAVORITE_MUTATION, () => ({
    variables,
    options
  }));

  onError(err => {
    ElMessage.error(`${errorMessage(err)}`);
  });

  return {
    toggleFavorite,
    onDone,
    onError,
    loading
  };
}
