export const prefix = window.location.host;

export const StorageKeys = {
  user: 'user',
  searchLogs: 'searchLogs',
  seos: 'seos'
} as const;
export type ItemKeys = typeof StorageKeys;

function keyGenerator(key) {
  return prefix + '-' + key;
}

export default class Storage {
  static setItem(key: keyof ItemKeys, value: any, maxAge?: number) {
    const KEY = keyGenerator(key);
    const VALUE = {
      value,
      maxAge,
      timestamp: Date.now()
    };
    localStorage.setItem(KEY, JSON.stringify(VALUE));
    // console.log('localStorage.setItem', KEY, '：', VALUE);
  }

  static getItem(key: keyof ItemKeys) {
    const KEY = keyGenerator(key);
    let item: any = localStorage.getItem(KEY);
    try {
      item = JSON.parse(item);
    } catch (err) {}
    // console.log('localStorage.getItem', key, '：', item);
    if (item && item.value) {
      if (item.maxAge && Date.now() - item.timestamp > item.maxAge * 1000) {
        localStorage.removeItem(key);
        return null;
      } else {
        return item.value;
      }
    }
  }

  static removeItem(key: keyof ItemKeys) {
    const KEY = keyGenerator(key);
    localStorage.removeItem(KEY);
    // console.log('localStorage.removeItem', KEY);
  }

  static clear() {
    // localStorage.clear();
  }
}
