import { gql } from '@apollo/client/core';
import { POST_FRAGMENT } from './post';

export const COLLECTION_FRAGMENT = gql`
  fragment collectionFragment on Collection {
    id
    logo
    name
    description
    count_articles
    count_follows
    count_posts
    count_views
    updated_to_episode
    followed
    user {
      id
      name
      avatar
    }
    movie {
      id
      name
      introduction
      cover
    }
  }
`;

export const COLLECTION_QUERY = gql`
  ${COLLECTION_FRAGMENT}

  query randomCollectionsQuery($page: Int) {
    randomCollections(page: $page) {
      paginatorInfo {
        hasMorePages
        currentPage
      }
      data {
        ...collectionFragment
      }
    }
  }
`;

// # 合集内的视频
export const COLLECTION_POSTS_QUERY = gql`
  ${POST_FRAGMENT}
  ${COLLECTION_FRAGMENT}

  query collectionPostsQuery($id: Int!, $page: Int, $count: Int) {
    collection(collection_id: $id) {
      type
      status
      ...collectionFragment
      posts(page: $page, count: $count) {
        paginatorInfo {
          lastPage
          currentPage
          hasMorePages
        }
        data {
          ...postFragment
          collections {
            ...collectionFragment
          }
        }
      }
    }
  }
`;
