<template>
  <div id="login-dialog" v-loading.fullscreen.lock="loading">
    <el-dialog
      v-model="visible"
      custom-class="login-modal"
      :title="activeName === '登录' ? '账号登录' : activeName === '重置密码' ? '重置密码' : '注册账号'"
      width="90%"
      destroy-on-close
      center
    >
      <div v-if="activeName === '重置密码'">
        <ResetPassword :phone="formData.account" :toggleTab="toggleActiveTab" />
      </div>
      <div v-else>
        <el-form ref="loginForm" :model="formData" :rules="rules" class="login-form">
          <el-form-item label="手机号" prop="account">
            <el-input
              name="formData-account"
              autocomplete
              class="account-input"
              v-model="formData.account"
              placeholder="请输入手机号"
              clearable
              maxlength="11"
            >
              <template #prefix>
                +86
              </template>
            </el-input>
          </el-form-item>
          <el-form-item label="密&emsp;码" prop="password">
            <el-input v-model="formData.password" placeholder="请输入密码" show-password maxlength="12"></el-input>
          </el-form-item>
          <div class="form-label">
            <p class="tab-item" @click="toggleForm">
              <span class="label-btn">
                {{ activeName === '登录' ? '没有账号？去注册' : '已有账号！去登录' }}
              </span>
            </p>
            <p class="tab-item" v-if="activeName === '登录'" @click="sendVerifyCode">
              忘记密码?
            </p>
          </div>
          <el-form-item class="button-wrap">
            <el-button type="primary" :disabled="disabled" @click="submitForm" class="submit-button">
              {{ activeName }}
            </el-button>
          </el-form-item>
        </el-form>
        <p class="agreement">
          登录即表示已阅读并同意
          <a class="link-text" href="/userProtocol" target="_blank">《用户协议》</a> 和
          <a class="link-text" href="/privacyPolicy" target="_blank">《隐私政策》</a>
        </p>
      </div>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue';
import { ElMessage } from 'element-plus';
import { useMutation } from '@vue/apollo-composable';
import { exceptionCapture, errorMessage } from '@/common';
import { SEND_VERIFY_CODE, useSignInMutation, useSignUpMutation } from '@/graphql';
import store, { Storage, TYPES } from '@/store';
import ResetPassword from './ResetPassword.vue';

export default defineComponent({
  components: { ResetPassword },
  setup(props, context) {
    const visible = ref(false);
    const activeName = ref('登录');
    const loading = ref(false);
    const formData = ref({ account: '', password: '' });
    const { signInMutate } = useSignInMutation(formData.value);
    const { signUpMutate } = useSignUpMutation(formData.value);

    const { mutate: sendCodeMutation } = useMutation(SEND_VERIFY_CODE, () => ({
      variables: {
        phone: formData.value.account,
        action: 'RESET_PASSWORD'
      }
    }));

    function hideModal() {
      visible.value = false;
    }
    function toggleActiveTab() {
      const tabName = activeName.value === '登录' ? '注册' : '登录';
      activeName.value = tabName;
    }

    function onError(err) {
      ElMessage.error(`${activeName.value}出错：${errorMessage(err)}`);
    }

    function onSuccess(res) {
      ElMessage({
        message: `${activeName.value}成功!`,
        type: 'success'
      });
      hideModal();
      store.commit(TYPES.USER_LOGIN, res);
      Storage.setItem('user', res);
      // window.location.reload();
    }

    async function onSignIn() {
      loading.value = true;
      const [err, res] = await exceptionCapture(() => signInMutate());
      loading.value = false;
      if (err) {
        console.log('signIn err', err);
        onError(err);
      } else if (res?.data.signIn) {
        onSuccess(res?.data.signIn);
      }
    }

    async function onSignUp() {
      loading.value = true;
      const [err, res] = await exceptionCapture(() => signUpMutate());
      loading.value = false;
      if (err) {
        onError(err);
      } else if (res?.data.signUp) {
        onSuccess(res?.data.signUp);
      }
    }

    async function sendVerifyCode() {
      if (formData.value.account) {
        const [err, res] = await exceptionCapture(() => sendCodeMutation());
        if (err) {
          ElMessage.error(`获取验证码出错：${errorMessage(err)}`);
        } else if (res?.data.sendVerifyCode) {
          activeName.value = '重置密码';
          ElMessage({ message: '验证码发送成功!', type: 'success' });
        }
      } else {
        ElMessage('请先输入手机号!');
      }
    }

    return {
      visible,
      activeName,
      toggleActiveTab,
      loading,
      formData,
      hideModal,
      onSignIn,
      onSignUp,
      sendVerifyCode
    };
  },
  computed: {
    disabled() {
      return !this.formData.account || !this.formData.password;
    }
  },
  mounted() {
    this.$bus.on('SHOW_LOGIN_MODAL', () => {
      this.activeName = '登录';
      this.visible = true;
    });
  },
  methods: {
    submitForm() {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          if (this.activeName === '登录') {
            this.onSignIn();
          } else {
            this.onSignUp();
          }
        } else {
          return false;
        }
      });
    },
    toggleForm() {
      this.$refs.loginForm.resetFields();
      this.toggleActiveTab();
    }
  },
  data() {
    return {
      rules: {
        account: [
          {
            required: true,
            message: '请输入手机号',
            trigger: 'blur'
          },
          {
            min: 11,
            message: '手机号长度不正确',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 12,
            message: '密码格式不正确（6-12）位',
            trigger: 'blur'
          }
        ]
      }
    };
  }
});
</script>

