<template>
  <div class="movies-history">
    <template v-for="item in moviesHistory" :key="item.id">
      <router-link v-if="item.movie?.id" class="item-wrap" :to="`/movie/${item.movie.id}`">
        <HTMovieItem :movie="item.movie" :time-ago="item.last_watch_time" :mini="true" />
      </router-link>
    </template>
    <div v-if="!disabled" class="view-all">
      <router-link :to="`/user/${user.id}?tab=history`">
        <div class="button button-secondary">
          查看全部
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref } from 'vue';
import { useMoviesHistoryQuery } from '@/graphql';
import HTMovieItem from '@/components/HTMovieItem.vue';

export default {
  components: {
    HTMovieItem
  },
  setup(props) {
    const { loading, error, disabled, moviesHistory } = useMoviesHistoryQuery();
    return {
      loading,
      error,
      disabled,
      moviesHistory
    };
  },
  computed: {
    user() {
      return this.$store.state.user.user;
    }
  }
};
</script>

