<template>
  <div class="module-column" :data-rowcount="rowCount" :data-count="movies.length">
    <div v-if="!!title" class="mod-header">
      <div class="mod-title text-ellipsis">
        {{ title }}
      </div>
      <div class="mod-paging" v-if="totalPage > 1">
        <button @click="prev" :class="['btn_prev', currentPage === 1 && 'disabled']" title="上一页">
          <svg class="svg_icon svg_icon_prev" viewBox="0 0 6 10" width="6" height="10">
            <path
              d="M1.4 4.7L5 1M1.3 5.3L5 9"
              fill="none"
              stroke="currentColor"
              stroke-width="1.4"
              stroke-linecap="round"
            ></path>
          </svg>
        </button>
        <span class="page_num">{{ currentPage + '/' + totalPage }}</span>
        <button @click="next" :class="['btn_next', currentPage === totalPage && 'disabled']" title="下一页">
          <svg class="svg_icon svg_icon_next" viewBox="0 0 6 10" width="6" height="10">
            <path
              d="M4.6 4.7L1 1M4.7 5.3L1 9"
              fill="none"
              stroke="currentColor"
              stroke-width="1.4"
              stroke-linecap="round"
            ></path>
          </svg>
        </button>
      </div>
    </div>
    <div class="column-items">
      <div
        class="item-wrap"
        v-for="(item, index) in movies"
        :key="item.id"
        v-show="index >= minIndex && index < maxIndex"
      >
        <router-link :to="`/movie/${item.id}`">
          <MovieItem :movie="item" />
        </router-link>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue';
import { throttle } from 'lodash';
import MovieItem from '@/components/MovieItem.vue';

export default defineComponent({
  components: {
    MovieItem
  },
  props: {
    title: {
      type: String
    },
    movies: {
      type: Array
    }
  },
  mounted() {
    window.addEventListener('resize', throttle(this.getColumnItems, 200));
    this.getColumnItems();
  },
  methods: {
    getColumnItems() {
      const container = document.getElementById('app');
      var styles = window.getComputedStyle(container);
      this.rowCount = styles.getPropertyValue('--column-movie-items-per-row');
    },
    prev() {
      this.currentPage--;
    },
    next() {
      this.currentPage++;
    }
  },
  computed: {
    totalPage() {
      return Math.ceil(this.movies.length / this.rowCount);
    },
    maxIndex() {
      return this.rowCount * this.currentPage;
    },
    minIndex() {
      const remainder = this.movies.length % this.rowCount;
      if (this.totalPage === this.currentPage && remainder > 0) {
        return this.rowCount * (this.currentPage - 2) + remainder;
      }
      return this.rowCount * (this.currentPage - 1);
    }
  },
  data() {
    return {
      rowCount: 3,
      currentPage: 1
    };
  }
});
</script>

<style lang="scss" scoped>
