  .mod-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    .mod-title {
      font-size: 2.2rem;
      font-weight: 500;
      color: var(--theme-text-primary);
      @media (max-width: $col-sm-width - 1px) {
        font-size: 2rem;
      }
    }
  }

  .mod-paging {
    display: flex;
    .btn_prev,
    .btn_next {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 30px;
      height: 30px;
      border-radius: 100%;
      color: #666;
      text-align: center;
      vertical-align: top;
      .svg_icon {
        width: 12px;
        height: 12px;
      }
      &.disabled {
        color: #ccc;
        pointer-events: none;
      }
      &:hover {
        background-color: var(--theme-general-background-a);
        color: $theme-color_secondary;
      }
    }
    .page_num {
      display: inline-block;
      min-width: 20px;
      margin: 0 15px;
      color: #666;
      font-size: 14px;
      letter-spacing: 2px;
      line-height: 30px;
      text-align: center;
      vertical-align: top;
    }
  }
}
</style>
