import { gql } from '@apollo/client/core';
import { POST_FRAGMENT } from './post';

export const MOVIE_FRAGMENT = gql`
  fragment movieFragment on Movie {
    id
    name
    introduction
    cover
    producer
    year
    type
    style
    region
    actors
    count_series
    country
    lang
    hits
    score
    created_at
    movie_key
    state
    favorited
    last_watch_series
    last_watch_progress
    count_comments
    count_favorites
  }
`;

// 电影详情
export const MOVIE_QUERY = gql`
  ${MOVIE_FRAGMENT}
  ${POST_FRAGMENT}

  query movieQuery($id: Int, $key: String) {
    movie(movie_id: $id, movie_key: $key) {
      ...movieFragment
      user {
        id
        name
        avatar
        followed_status
      }
      series {
        name
        url
      }
      play_lines {
        name
        data
      }
      jieshuo {
        ...postFragment
      }
      clips {
        ...postFragment
      }
    }
  }
`;

// 影片的相关推荐
export const RELATED_MOVIES_QUERY = gql`
  ${MOVIE_FRAGMENT}

  query relatedMoviesQuery($movie_id: Int!, $limit: Int) {
    relatedMovies(movie_id: $movie_id, limit: $limit) {
      ...movieFragment
    }
  }
`;

// # 观看历史记录
export const MOVIES_HISTORY_QUERY = gql`
  ${MOVIE_FRAGMENT}
  query showMovieHistoryQuery($first: Int = 10, $page: Int) {
    showMovieHistory(first: $first, page: $page) {
      paginatorInfo {
        hasMorePages
        currentPage
      }
      data {
        id
        progress
        series_index
        last_watch_time
        movie {
          ...movieFragment
        }
      }
    }
  }
`;

// 用户在追的剧
export const FAVORITE_MOVIES_QUERY = gql`
  ${MOVIE_FRAGMENT}

  query favoriteMoviesQuery($user_id: Int, $type: FavorableEnum = movies, $first: Int, $page: Int) {
    userFavorites(user_id: $user_id, type: $type, first: $first, page: $page) {
      paginatorInfo {
        currentPage
        hasMorePages
      }
      data {
        id
        movie {
          ...movieFragment
        }
      }
    }
  }
`;

// 编辑精选的电影
export const EDITOR_CHOICE_MOVIES_QUERY = gql`
  ${MOVIE_FRAGMENT}

  query editorChoiceQuery($title: String!, $page: Int) {
    editorChoice(title: $title) {
      id
      title
      summary
      editor {
        id
        age
        name
        avatar
      }
      activities {
        image_url
        subtitle
        movie {
          id
          name
          actors
          movie_key
        }
      }
      movies(page: $page, first: 12) {
        paginatorInfo {
          hasMorePages
          currentPage
        }
        data {
          ...movieFragment
        }
      }
    }
  }
`;

// 获取指定位置置顶数据
export const STICKS_QUERY = gql`
  ${MOVIE_FRAGMENT}

  # 获取指定位置置顶数据
  query sticksQuery($place: String, $page: Int, $movies_page: Int) {
    sticks(place: $place, page: $page) {
      paginatorInfo {
        hasMorePages
        currentPage
      }
      data {
        id
        cover
        place
        editorChoice {
          id
          title
          summary
          activities {
            id
            title
            image_url
            subtitle
            movie {
              id
              name
              actors
              movie_key
              series {
                name
                url
              }
            }
            activityable_type
            activityable_id
            image_url
          }

          editor {
            id
            age
            name
            avatar
          }
          movies(page: $movies_page, first: 12) {
            paginatorInfo {
              hasMorePages
              currentPage
            }
            data {
              ...movieFragment
              custom_type
            }
          }
        }
      }
    }
  }
`;

// 分类筛选出来的电影
export const CATEGORY_MOVIE_QUERY = gql`
  ${MOVIE_FRAGMENT}

  query categoryMovieQuery(
    $region: MovieCategory
    $type: String
    $style: String
    $country: String
    $lang: String
    $first: Int
    $page: Int
    $year: String
    $scopes: ScopesType
  ) {
    categoryMovie(
      region: $region
      type: $type
      country: $country
      lang: $lang
      style: $style
      first: $first
      page: $page
      year: $year
      scopes: $scopes
    ) {
      paginatorInfo {
        hasMorePages
        currentPage
      }
      data {
        ...movieFragment
      }
    }
  }
`;

// 影视片单
export const CHANNELS_QUERY = gql`
  ${MOVIE_FRAGMENT}

  query channelsQuery($page: Int, $first: Int, $sort: Sort = LASTEST) {
    channels(page: $page, first: $first, sort: $sort) {
      paginatorInfo {
        total
        hasMorePages
        currentPage
      }
      data {
        id
        title
        sketch
        cover
        state
        click_count
        serie_count
        movie {
          ...movieFragment
        }
      }
    }
  }
`;

// # 搜索影视 - 性能优化 + 搜索json结果兼容
export const SEARCH_MOVIES_QUERY = gql`
  ${MOVIE_FRAGMENT}

  query searchMoviesQuery($keyword: String!, $page: Int) {
    searchMovies(keyword: $keyword, page: $page) {
      paginatorInfo {
        total
        currentPage
        hasMorePages
      }
      data {
        ...movieFragment
        series {
          name
          url
        }
      }
    }
  }
`;

// # 大家都在搜
export const SEARCH_LOGS_QUERY = gql`
  query searchLogsQuery {
    searchLogs
  }
`;

// 保存观看记录
export const SAVE_WATCH_PROGRESS_MUTATION = gql`
  mutation saveWatchProgressMutation($movie_id: Int!, $series_index: Int, $progress: Int) {
    saveWatchProgress(movie_id: $movie_id, series_index: $series_index, progress: $progress)
  }
`;
