// @ts-nocheck

import { Ref } from 'vue-demi';
import { ref, onMounted, watch } from 'vue';
import { useQuery, useResult, UseQueryOptions } from '@vue/apollo-composable';
import { SEARCH_MOVIES_QUERY } from '@/graphql';

interface Variables {
  keyword: Ref<String> | String;
  page?: Ref<Number> | Number;
}

export default function useSearchMoviesQuery(
  variables: Ref<Variables> | Variables,
  options?: UseQueryOptions<any, Variables>
) {
  const { result, loading, error, refetch } = useQuery(SEARCH_MOVIES_QUERY, variables, options);
  const searchMovies = useResult(result, [], (data: any) => data?.searchMovies?.data);
  const total = useResult(result, {}, (data: any) => data?.searchMovies?.paginatorInfo?.total);

  return {
    loading,
    error,
    refetch,
    searchMovies,
    total
  };
}
