<template>
  <el-button round :size="size" :type="isFollowed ? 'info' : 'primary'" v-auth:click="toggleFollow" :loading="loading">
    {{ isFollowed ? '已关注' : '关 注' }}
  </el-button>
</template>

<script lang="ts">
import { ref, toRefs, watch } from 'vue';
import { useStore } from 'vuex';
import { ElMessage } from 'element-plus';
import { useMutation } from '@vue/apollo-composable';
import { exceptionCapture } from '@/common';
import { TOGGLE_FOLLOW_MUTATION, FOLLOWED_USERS_QUERY, USER_FOLLOWERS_QUERY, USER_QUERY } from '@/graphql';

export default {
  props: {
    user: {
      type: Object
    },
    size: [String, undefined, null]
  },
  setup(props: { user: Record<string, any>; size: String | undefined | null }) {
    const store = useStore();
    const { user, size } = toRefs(props);
    const me = ref(store.state?.user?.user);
    const isFollowed = ref(user.value?.followed_status);

    const { mutate: toggleFollowMutation, loading } = useMutation(TOGGLE_FOLLOW_MUTATION, () => ({
      variables: { id: user.value?.id, type: 'users' },
      refetchQueries: () => [
        {
          query: USER_QUERY,
          variables: { id: me.value?.id }
        }
      ]
    }));

    async function toggleFollow() {
      isFollowed.value = !isFollowed.value;
      const [err, res] = await exceptionCapture(() => toggleFollowMutation());
      if (err) {
        ElMessage.error(isFollowed.value ? '关注失败' : '取关失败');
        isFollowed.value = !isFollowed.value;
      }
    }

    // watch(user, value => {
    //   isFollowed.value = value?.followed_status;
    // });

    return { size, isFollowed, toggleFollow, loading };
  }
};
</script>

<style lang="scss"></style>
