import { Ref } from 'vue-demi';
import { ref, onMounted, watch } from 'vue';
import { useQuery, useMutation, UseMutationOptions } from '@vue/apollo-composable';
import { SIGN_IN } from '@/graphql';

interface Variables {
  account: String;
  password: String;
}

export default function useSignInMutation(variables: Variables, options?: UseMutationOptions) {
  const user = ref({});
  const { mutate: signInMutate, onDone, onError, loading } = useMutation(SIGN_IN, () => ({ variables, options }));

  onDone(res => {
    user.value = res?.data?.signIn;
  });

  return {
    signInMutate,
    onDone,
    onError,
    loading,
    user
  };
}
