
import { defineComponent, onMounted, ref } from 'vue';
import { useRouter, useRoute } from 'vue-router';
import { useApolloClient } from '@vue/apollo-composable';
import { exceptionCapture } from '@/common';
import store, { Storage, TYPES } from '@/store';
import { SEARCH_MOVIES_QUERY } from '@/graphql';

export default defineComponent({
  setup(props) {
    const router = useRouter();
    const route = useRoute();
    const client = useApolloClient().client;
    const keyword = ref(route.query?.keyword);
    const searchLogs = ref(Storage.getItem('searchLogs') || []);
    const searchResult = ref();
    const deleteSearchLogs = queryString => {
      const index = searchLogs.value.indexOf(queryString);
      if (index >= 0) {
        searchLogs.value.splice(index, 1);
      } else {
        searchLogs.value = [];
      }
      Storage.setItem('searchLogs', searchLogs.value);
    };
    const saveSearchLogs = queryString => {
      const newLogs = new Set([queryString, ...searchLogs.value]);
      searchLogs.value = [...newLogs].slice(0, 10);
      Storage.setItem('searchLogs', searchLogs.value);
    };

    const querySearch = async (queryString: string, cb) => {
      const result = await (queryString ? searchMovies(queryString) : searchLogs.value);
      searchResult.value = result;
      // 调用 callback 返回列表数据
      cb(result);
    };
    const searchMovies = async (queryString: string) => {
      const [err, res] = await exceptionCapture(() =>
        client.query({ query: SEARCH_MOVIES_QUERY, variables: { keyword: queryString } })
      );
      return res?.data?.searchMovies?.data || [];
    };

    // 输入建议框显示/消失
    const changeStyle = (status, className) => {
      let dom = document.querySelectorAll(className);
      dom[0].style.display = status;
    };

    const onSearch = ({ q }) => {
      const text = q || keyword.value || '';
      if (text) {
        saveSearchLogs(text);
        keyword.value = text;
        router.push({ path: '/search', query: { keyword: text } });
        changeStyle('none', '.el-autocomplete-suggestion');
      }
    };

    onMounted(() => {
      keyword.value = route.query?.keyword || '';
    });

    return {
      keyword,
      querySearch,
      searchLogs,
      searchResult,
      onSearch,
      changeStyle,
      deleteSearchLogs
    };
  }
});
