
import { TYPES } from '@/store';
import SearchBar from '@/components/SearchBar.vue';
import HistoryPopover from '@/views/parts/HistoryPopover.vue';

export default {
  components: {
    SearchBar,
    HistoryPopover
  },
  computed: {
    logged() {
      return this.$store.getters.logged;
    },
    user() {
      return this.$store.state.user.user;
    }
  },
  methods: {
    showLoginModal() {
      if (screen.width >= 576) {
        this.$bus.emit('SHOW_LOGIN_MODAL');
      } else {
        this.$router.push('/login');
      }
    },
    handleLogout() {
      // this.loading = true;
      this.$store.commit(TYPES.USER_LOGOUT);
      // window.location.reload();
    }
  },
  data() {
    return {
      loading: false
    };
  }
};
