import * as TYPES from '../mutation-types';
import Storage from '../storage';

export default {
  state: {
    seos: { title: '', keywords: '', description: '' }
  },
  getters: {},
  mutations: {
    [TYPES.UPDATE_SEOS](state, payload) {
      if (payload) {
        state.seos = Object.assign({}, state.seos, payload);
        Storage.setItem('seos', state.seos);
      }
    }
  }
};
