import { Ref } from 'vue-demi';
import { ref, onMounted, watch } from 'vue';
import { useQuery, useResult, UseQueryOptions } from '@vue/apollo-composable';
import { POST_QUERY } from '@/graphql';

interface Variables {
  id: Ref<Number> | Number;
}
export default function usePostQuery(variables: Variables, options?: UseQueryOptions) {
  const { result, loading, error, refetch } = useQuery(POST_QUERY, variables, options);
  const post = useResult(result, null, (data: any) => data?.post);

  return {
    loading,
    error,
    refetch,
    post
  };
}
