import { createApp, provide, h } from 'vue';
import { ApolloClients } from '@vue/apollo-composable';
import axios from 'axios';
import ElementPlus from 'element-plus';
import './scss/element-plus.scss';
import './scss/theme.scss';
import './scss/common.scss';
import './registerServiceWorker';
import { apolloClient } from './graphql';
import router from './router';
import store from './store';
import App from './App.vue';
import EventBus from './common/eventBus';

const $bus = new EventBus();

const app = createApp({
  setup() {
    provide(ApolloClients, {
      default: apolloClient
    });
  },
  render: () => h(App)
});
// prototype
app.config.globalProperties.$bus = $bus;
app.config.globalProperties.$http = axios;
// directive
app.directive('auth', {
  mounted(el, binding) {
    const handler = function() {
      if (store.getters.logged) {
        binding.value.apply(this, arguments);
      } else {
        $bus.emit('SHOW_LOGIN_MODAL');
      }
    };
    const type = binding.arg;
    el.addEventListener(type, handler);
  }
});
// component
app.component('Masthead', require('./views/parts/Masthead.vue').default);
app.component('Header', require('./views/parts/Header.vue').default);
app.component('Footer', require('./views/parts/Footer.vue').default);
app.component('SideBar', require('./views/parts/SideBar.vue').default);
app.component('Empty', require('./components/Empty.vue').default);
app.component('FollowButton', require('./components/FollowButton.vue').default);

app
  .use(store)
  .use(router)
  .use(ElementPlus)
  .mount('#app');
