import { gql } from '@apollo/client/core';

const USER_FRAGMENT = gql`
  fragment userFragment on User {
    id
    age
    name
    avatar
    gender
    gender_msg
    birthday_msg
    introduction
    background
    count_likes
    count_articles
    count_posts
    count_followers
    count_followings
    count_favorites
    followed_status
    parent_id
    role
  }
`;

const SIGN_IN_FRAGMENT = gql`
  ${USER_FRAGMENT}

  fragment signInFragment on User {
    uuid
    token
    ...userFragment
    gold
    ticket
    balance
    exchangeRate
    total_contributes
    today_contributes
    phone
    title_phone
  }
`;

// 账号密码登录
export const SIGN_IN = gql`
  ${SIGN_IN_FRAGMENT}

  mutation signInMutation($account: String!, $password: String!, $uuid: String) {
    signIn(account: $account, password: $password, uuid: $uuid) {
      ...signInFragment
    }
  }
`;

// 注册
export const SIGN_UP = gql`
  ${SIGN_IN_FRAGMENT}

  mutation signUpMutation($account: String!, $password: String!, $name: String, $uuid: String) {
    signUp(account: $account, password: $password, name: $name, uuid: $uuid) {
      ...signInFragment
    }
  }
`;

// 获取验证码
// verifyType: RESET_PASSWORD | USER_INFO_CHANGE | USER_REGISTER | USER_LOGIN;
export const SEND_VERIFY_CODE = gql`
  mutation sendVerifyCodeMutation($phone: String!, $action: verifyType!) {
    sendVerifyCode(phone: $phone, action: $action) {
      id
      code
      account
    }
  }
`;

// 重置密码
export const RESET_PASSWORD = gql`
  mutation retrievePasswordMutation(
    $code: String!
    $phone: String
    $action: verifyType = RESET_PASSWORD
    $newPassword: String!
  ) {
    retrievePassword(code: $code, phone: $phone, action: $action, newPassword: $newPassword) {
      id
      name
      phone
      token
    }
  }
`;

// 修改用户信息
export const UPDATE_USER_INFO = gql`
  mutation updateUserInfoMutation($id: Int, $input: updateUserInfo!, $code: String) {
    updateUserInfo(id: $id, input: $input, code: $code) {
      id
      name
      avatar
      gender
      gender_msg
      birthday_msg
      introduction
    }
  }
`;

export const USER_QUERY = gql`
  ${USER_FRAGMENT}

  query userQuery($id: Int!) {
    user(id: $id) {
      ...userFragment
      phone
    }
  }
`;
