import { gql } from '@apollo/client/core';

// # 评论列表
export const COMMENT_FRAGMENT = gql`
  fragment commentFragment on Comment {
    id
    body
    likes
    liked
    time_ago
    count_replies
    commentable_id
    user {
      id
      name
      avatar
    }
  }
`;

// # 评论列表
export const COMMENTS_QUERY = gql`
  ${COMMENT_FRAGMENT}

  query commentsQuery(
    $type: CommentType!
    $id: Int!
    $page: Int
    $first: Int
    $user_id: Int
    $order: [OrderByClause!]
  ) {
    comments(
      commentable_type: $type
      commentable_id: $id
      page: $page
      first: $first
      user_id: $user_id
      orderBy: $order
    ) {
      paginatorInfo {
        currentPage
        hasMorePages
        total
      }
      data {
        ...commentFragment
        # comments(first: 3) {
        #   data {
        #     ...commentFragment
        #   }
        # }
      }
    }
  }
`;

// # 子评论列表
export const COMMENT_REPLIES_QUERY = gql`
  ${COMMENT_FRAGMENT}

  query commentRepliesQuery($id: Int!, $page: Int, $first: Int) {
    comment(id: $id) {
      id
      comments(page: $page, first: $first) {
        paginatorInfo {
          currentPage
          hasMorePages
          total
        }
        data {
          ...commentFragment
        }
      }
    }
  }
`;

// # 发评论
export const ADD_COMMENT_MUTATION = gql`
  ${COMMENT_FRAGMENT}

  mutation addCommentMutation($type: CommentType!, $id: Int!, $body: String!) {
    addComment(commentable_type: $type, commentable_id: $id, body: $body) {
      ...commentFragment
    }
  }
`;

// 删除评论
export const DELETE_COMMENT_MUTATION = gql`
  mutation deleteCommentMutation($id: Int!) {
    deleteComment(id: $id)
  }
`;
