import { useMutation, UseMutationOptions } from '@vue/apollo-composable';
import { TOGGLE_LIKE_MUTATION } from '@/graphql';
import { errorMessage } from '@/common';
import { ElMessage } from 'element-plus';

interface Variables {
  id?: number | any;
  type?: string;
}

export default function useLikeMutation(variables: Variables, options?: UseMutationOptions) {
  const { mutate: toggleLike, onDone, onError, loading } = useMutation(TOGGLE_LIKE_MUTATION, () => ({
    variables,
    options
  }));

  onError(err => {
    ElMessage.error(`${errorMessage(err)}`);
  });

  return {
    toggleLike,
    onDone,
    onError,
    loading
  };
}
